.petition-wrapper{
    display: flex;
    flex-direction: column;
    // border: 2px solid black;
    width: 100%;
}

.petition {
    background-color: #f5f5f5;
    margin-left: auto;
    margin-right: auto;
    width: 80%;
    padding: 20px;
    margin-bottom: 40px;
    border-radius: 8px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  }
  
  .petition:hover{
    transform: scale(1.05); 
    transition: transform 0.6s ease; 
    box-shadow: 0 8px 16px rgba(0, 0, 0, 0.3);
  }
  .petition .title {
    display: flex;
    justify-content: space-between;
    font-size: 20px;
    font-weight: bold;
    margin-bottom: 10px;
  }
  
  .petition .title span:first-child {
    color: #333;
    margin-right: 5px;
  }
  
  .petition .petition_text {
    font-size: 18px;
    line-height: 1.6;
    color: #555;
  }

  .text_buton_wrapper{
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 20px;
  }

  .text_buton_wrapper:first-child{
    margin-left: auto;
    margin-right: auto;
  }

  .text_buton_wrapper button{
    position: absolute;
    right: 10%;
  }

  @media screen and (max-width: 1100px) {
    .text_buton_wrapper{
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      align-items: center;
    }
    .text_buton_wrapper button{
      position: relative;
      right:unset;
      width: 60%;
    }
  }


