nav ul {
    list-style: none;
    display: flex;

    // border: 2px solid red;
    width: 600px;
    justify-content: space-around;


}
nav ul a {
    color: white;
    transition: all 0.2s linear;
    text-decoration: none;
}

nav ul a:hover{
    text-decoration: underline;
    color: #6dd4b1;
}

@media screen and (max-width: 1150px){
    nav{
        display: none
    }

}