.grid {
  font-size: 3rem;
  // border: 2px solid black;
  width: 100%;
  display: flex;
  justify-content: space-around;
}

.grid_text {
  font-weight: bold;
}

.container {
  width: 100%;
  // border: 2px solid red;
}

.card-container {
  display: flex;
  justify-content: center;
  align-items: center;
}
a{
  text-decoration: none;
}

.card {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 300px;
  margin: 10px;
}

.card-media {
  height: 100px;
}
.image{
  width: 60%;
  margin-left: 20%;
}
.image_petition{
  margin-left: 30%;
  width: 40%;
}


.tournaments {
  text-align: center;
}

.sport-image {
  object-fit: contain;
}
.grid_text {
  font-weight: bold;
  font-size: 6vw; /* Размер шрифта будет 6% от ширины экрана */
}
.link_petition{
  text-decoration: none;
}

@media screen and (max-width: 1100px) {
  .card {
    width: 50px;
  }

  .grid{
    display: flex;
    justify-content: center;
    text-align: center;
    height: auto;
  }
  .grid img {
      display: none;
  }
  .card-media {

    height: 100px;
  }
  .image{
    width: 60%;
    margin-left: 20%;
  }

}