
// --------------------   left side --------------------------------------------
main{
    position: relative;
    height: 1300px;
}
.left{
    float: left;
    width: 70%;
}
.register_cards{
    width: 35vw;
    height: 380px;
}
.tournaments_news{
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    gap: 50px
}
.tournaments_news_card{
    width: 25vw;
}
.read_more:hover{
    text-decoration: underline;
    cursor: pointer;
}

// --------------------   right side --------------------------------------------
.right{
    float: left;
    position: inherit;
    width: 30%;
    height: 100%;
}

.tournaments_event_wrapper{
    display: flex;
    align-items: center;
    flex-direction: column;
    position: sticky;
    top: 70px;
    gap: 15px
}
.tournaments_event{
    width: 25vw;
    height: 80px;
}
.tournament_name_div{
    display: flex;
    justify-content: space-between;
    align-items: center;
}
.date_div{
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;
    top: -3px;
    background-color: white;
    color: black;
    width: 50px;
    height: 20px;
    font-size: 16px;
    font-weight: bold;
    border-radius: 5px;
}

@media screen and (min-width: 1600px){
    .tournaments_news_card{
        width: 25vw;
        height: 350px;
    }
}

@media screen and (max-width: 1200px) {
    .left{
        float: unset;
        width: 100%;
    }
    .right{
        float: left;
        position: inherit;
        width: 100%;
        height: 100%;
    }
}




