.image_1{
    width: 10%;
}
.title_about{
    margin-left: auto;
    margin-right: auto;
    display: flex;
    align-items: center;
    justify-content: space-around;
    width: 80%;
}

.button_join{
    background-color: #1C1C84 !important;
    color: #fff;
}
.message_container {
    margin-top: 10px;
    width: 80%;
    margin-left: auto;
    margin-right: auto;
    text-align: justify;
    background-color: #e8e6e6;
    border-radius: 5px;
}

@media screen and (max-width: 1150px) {
    .image_1{
        width: 40%;
    }
    .title_about{
        margin-top: 20px;
        margin-left: auto;
        margin-right: auto;
        display: flex;
        align-items: center;
        justify-content: space-around;
        width: 100%;
        margin-bottom: 30px;
        flex-wrap: wrap;
    }
    .title_about button{
        margin-top: 20px;
    }
}